* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
body{
  background-color: #f5f5f5;
}
@media (max-width:560px) {
  .headerMenu {
    display: none;
  }

  .img-carousel {
    object-fit: fill;
    height: 200px;
  }

  .videoPlayer {
    width: 100%;
    margin: auto;
    height: 30vh;
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .loginForm {
    width: 80%;
    margin: auto;
    padding-top: 30px;
    text-align: center;
  }
  .search-input{
    outline: none;
    padding: 0 15px;
    font-size: 20px;
    width: 90%;
    height: 50px;
    border-radius: 15px;
  }
  .search-suggestion{
    margin: auto;
    width: 80%;
    text-align: left;
    margin-top: 5px;
    height: 500px;
    overflow: hidden;
    color: #fff;
    line-height: 25px;
  }
}

@media (min-width:560px) {
  .img-carousel {
    object-fit: fill;
    height: 350px;
  }

  .videoPlayer {
    width: 95%;
    margin: auto;
    height: 75vh;
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .loginForm {
    width: 40%;
    margin: auto;
    padding-top: 30px;
    text-align: center;
  }
  .search-input{
    outline: none;
    padding: 0 15px;
    width: 50%;
    height: 60px;
    font-size: 20px;
    border-radius: 15px;
  }
  .drawer-menu{
    display: none;
  }
  .search-suggestion{
    margin: auto;
    width: 40%;
    text-align: left;
    margin-top: 5px;
    height: 300px;
    overflow: hidden;
    color: #fff;
    line-height: 25px;
  }
}

.logo {
  float: left;
}

.ant-menu-item-selected {
  background-color: rgb(9, 0, 139) !important;
  color: white !important;
  border-radius: 10px !important;
}
.ant-menu-item:hover:not(.ant-menu-item-selected){
  color: rgba(235, 81, 189, 0.89) !important;
}
.ant-menu-horizontal, .ant-menu-item::after, .ant-menu-submenu::after {
  border: none !important;
}
.overlay {
  height: 100vh;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.9);
}

.overlay-content {
  position: relative;
  top: 18%;
  width: 80%;
  text-align: center;
  margin-top: 30px;
  margin: auto;
}

.suggestedText:hover{
  color: rgb(199, 187, 164);
}
.overlay .closebtn {
  position: absolute;
  top: 80px;
  right: 45px;
  cursor: pointer;
  color: white;
}

.overlay .closebtn:hover {
  color: #ccc;
}
.dropdown{
  width: 200px;
  background-color: aquamarine;
}
@media (min-width:1024px) {
  .searchInput {
    width: 500px;
  }
}

.carousel-text {
  padding: 10px 50px;
  position: relative;
  border-radius: 10px;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  text-shadow: 1px 1px black;
  overflow: hidden;
}

.dataText {
  font-size: 15px;
  color: #fff;
  margin-top: 10px;
  font-weight: bold;
  text-shadow: 1px 1px black;
  line-height: 25px;
  font-family: 'Times New Roman', Times, serif;
}

.movieTitle {
  font-weight: bolder;
  color: red;
}

@media screen and (orientation:landscape) {
  .videoPlayer {
    margin-top: 50px;
    margin-bottom: 100px;
  }
}